* {
  box-sizing: border-box;
}

html {
  --placeholder-color: #3d4e61;
}

@font-face {
  font-family: Avenir-book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

body {
  margin: 0;
  font-weight: 300;
}

.search-bar {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  width: calc(100% - 16px);
  margin: 0 8px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar input {
  color: var(--placeholder-color);
  font-weight: bold;
}

.search-bar input::placeholder {
  color: var(--placeholder-color);
  font-weight: normal;
}

.search-bar--big {
  height: 70px;
  margin-top: 20px;
}

.search-bar--big input {
  color: var(--placeholder-color);
  background-image: url("Search.ccd3c07b.svg");
  background-position: 26px 25px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  border: none;
  width: 100%;
  height: 100%;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 62px;
  font-family: Avenir-Book, sans-serif;
  font-size: 16px;
  line-height: 22px;
}

.search-bar--small {
  height: 42px;
  margin-top: 20px;
}

.search-bar--small input {
  color: var(--placeholder-color);
  background-image: url("Search.ccd3c07b.svg");
  background-position: 13px 15px;
  background-repeat: no-repeat;
  background-size: 11px 11px;
  border: none;
  width: 100%;
  height: 100%;
  padding-left: 33px;
  font-family: Avenir-Book, sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar--big input:focus {
  background: url("Search.ccd3c07b.svg") 26px 25px / 19px 19px no-repeat, linear-gradient(#0000, #f6f6f7);
  outline: none;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar--small input:focus {
  background: url("Search.ccd3c07b.svg") 13px 15px / 11px 11px no-repeat, linear-gradient(#0000, #f6f6f7);
  outline: none;
  box-shadow: 0 4px 4px #00000040;
}
